/*eslint no-unused-vars: "off"*/
/*eslint-disable @typescript-eslint/no-unused-vars */

import React, { useState } from "react"
import { HLocalStorage } from "src/core/helpers"
const kCategoryScreen = "CategoryScreen"
const PageDev = () => {
  const categoryScreen = HLocalStorage.getObj(kCategoryScreen, [])
  // console.warn("categoryScreen:", categoryScreen)
  return (
    <>
      <div className="d-flex flex-column align-items-stretch flex-shrink-0 bg-white">
        <div></div>
        <div className="list-group list-group-flush border-bottom scrollarea">
          {categoryScreen &&
            categoryScreen.length > 0 &&
            categoryScreen.map((v, i) => {
              return (
                <a
                  key={i}
                  href={v.UIUrl}
                  className="list-group-item list-group-item-action py-3 lh-tight"
                >
                  <div className="d-flex w-100 align-items-center justify-content-between">
                    <strong>{v.Title}</strong>
                    <small>{v.ScreenCode}</small>
                  </div>
                  <div className="col-10 mb-1 small">{v.UIUrl}</div>
                </a>
              )
            })}
        </div>
      </div>
    </>
  )
}

export default PageDev
